}

.footer__link {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  position: relative;
  padding: 0;
  color: #ffffff;
  background: none;
  border: none;
  font-size: 14px;
  line-height: 22px;
}
@media (max-width: 767px) {
  .footer__link {
    font-size: 16px;
  }
}
.footer__link:hover::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 1px;
  background-color: #ffffff;
}
.footer__link:focus-visible {
  outline: 1px solid #ffffff;
  outline-offset: 5px;
}
.footer__link:active {
  outline: none;
}
.footer__link:active::after {
  content: none;
}

.footer__social-list {
  margin: 0;
  display: flex;
  list-style-type: none;
  gap: 11px;
}
@media (max-width: 767px) {
  .footer__social-list {
    padding: 0;
    justify-content: start;
    column-span: all;
  }
}

.footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
.footer__social-link:hover, .footer__social-link:focus-visible {
  fill: #ffffff;
  outline: none;
}
.footer__social-link:active {
  background-color: #102564;
}
.footer__social-link:disabled {
  background-color: #4f545f;
  fill: #030a1a;
  pointer-events: none;
