  background-image: linear-gradient(rgba(20, 54, 122, 0.5)), url("../img/gym-video.png");
  background-repeat: no-repeat;
  background-position: center;
}
.gym__video.ready .gym__video-play {
  display: none;
}
.gym__video iframe {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.gym__video-play {
  outline: none;
  align-self: center;
  margin: 0 auto;
  border-radius: 50px;
  background: url("../../img/sprite/player-button.svg");
  border: none;
  width: 100px;
  height: 100px;
}
.gym__video-play:hover {
  cursor: pointer;
}
.gym__video-play:hover {
  background: url("../../img/sprite/player-button-hover.svg");
}
.gym__video-play:active {
  background: url("../../img/sprite/player-button-active.svg");
}
.gym__video-play:disabled {
  background: url("../../img/sprite/player-button-disabled.svg");
}

